html {scroll-behavior: smooth;}
body{
    font-size: 16px;
    font-family: 'Martel Sans', sans-serif;
    overflow-x: hidden;
    padding-top: 60px;
    line-height: 1.5;
}
h1,h2,h3,h4,h5,h6 {
    font-weight: 600;
    line-height: 1.5;
    font-family: 'Martel Sans', sans-serif;
    text-transform: uppercase;
    color: #000;
}
h1, .h1 {font-size: 1.75em;}
h2, .h2 {font-size: 1.7em;}
h3, .h3 {font-size: 1.15em;}
h4, .h4 {font-size: 1.1em;}
h5, h6, .h5, .h6{font-size: 1em; }

p,li {color: #444; font-size: 1em;}
a {color: #444; text-decoration: none;  transition: 0.3s;}

#myBtn {
    display: none;
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 99;
    font-size: 18px;
    border: none;
    outline: none;
    background-color: #3face4;
    cursor: pointer;
  }
  .topbtn button:hover{
      background: #000;
      color: #fff;
  }

  a.mainbtn {
    /*font-family: 'Abel', sans-serif;*/
    width: 170px;
    height: 40px;
    line-height: 30px;
    text-align: center;
    text-transform: uppercase;
    font-size: 14px;
    border: none;
    background: #146f9c;
    color: #fff;
    cursor: pointer;
    border-radius: 20px;
}
/* a.mainbtn:hover {
    background: #999;
    color: #000;
} */

/*--------section header start---------*/

.nav-link{color: #000; font-size: 0.9em; font-family: 'Martel Sans', sans-serif; text-transform: uppercase; font-weight: 600;}
.navbar-light .navbar-nav .nav-link:focus, .navbar-light .navbar-nav .nav-link:hover {color: #3face4;}
.bg-color{
    background: #111213;
    border-bottom: 4px solid #3face4;
  }
  
@media screen and (max-width: 480px) {
    .home_nav{padding-left: 8px;}
}

/*--------section header end---------*/

.banner-section{
    background-image: url(assets/banner.jpg);
    width: 100%;
    height: 100vh;
    height: auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 150px 0px;
}

.banner-title{
    color: #146f9c;
    font-size: 50px;
    font-weight: 600;
    text-shadow: 2px 4px rgba(50, 50, 70, 0.8);
    text-align: center;
}

.about-text{
    padding-top: 50px;
}


/* .client-data h4{
    font-size: 25px;
} */

.services-item .icon1 i {
    font-size: 50px;
    color: #146f9c;
    margin-bottom: 1rem;
}

.services-item {
    background: #fff;
    margin: 15px 0;
    padding: 30px;
    border-radius: 4px;
    text-align: left;
    box-shadow: 0 0 40px 0 rgba(0,0,0,.1);
    height:90%;
}

.contact-us{
    background: linear-gradient(90deg,rgb(71, 180, 194) 0%,#3378e7 100%);
    color: #fff;
}

.contact-text h1, h4{
    color: #fff;
}

.contact-text p{
    color: #fff;
}

.contact-text h1{
    color: #fff;
    font-size: 60px;
    font-weight: bolder;
}

.icon {
    font-size: 1.4rem;
    color: #000;
}

.social-link-whatsapp {
    background: #167e24;
}

.social-link-facebook {
    background: #4460ae;
}

.social-link-instagram {
    background: #cd4378;
}

.social-link-email{
    background: #dd2424;
}

.social-link {
    width: 30px;
    height: 30px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    color: #fff;
    text-decoration: none;
    font-size: 0.8rem;
}

a.social-link{
    text-decoration: none;
    color: #fff;
}

.btn-outline-primary {
    color: #3face4;
    border-color: #3face4;
}

.btn-outline-primary:hover {
    color: #fff;
    background-color: #3face4;
    border-color: #3face4;
}

.error{
	color: red;
  }



/*--------footer-area start---------*/
footer a { text-transform: uppercase; font-size: 0.8em; }
.footer-area{ 
    background-color:#000;
}

.footer-area a{ 
    color: #999;
    text-decoration: none;
}

.footer-bottom p{ 
    color: #999;
}

.footer-area .line{
    border: 1px;
    border-style: solid;
    border-color: #fff;
    text-align: left;
    width: 22%;
}

.footer-info li {
    padding-bottom: 10px;
    text-transform: uppercase;
    font-size: 16px;
}

.footer-contact li {
    padding-bottom: 10px;
    font-size: 16px;
}

.footer-widget h4.widget-title {
    font-size: 18px;
    text-transform: uppercase;
    color: #fff;
    font-weight: 600;
    /* padding-bottom: 30px; */
}

/*--------footer-area end---------*/

@media screen and (max-width: 600px){
    .banner-section{
        background-image: url(assets/banner-mobile.jpg);
        width: 100%;
        height: 100vh;
        height: auto;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        /* padding: 150px 0px; */
    }
    
    .banner-title{
        color: #146f9c;
        font-size: 45px;
        font-weight: 600;
        text-shadow: 2px 4px rgba(225, 225, 236, 0.8);
        text-align: center;
    }

    .about-text{
        padding-top: 10px;
        text-align: center;
    }


    .contact-text h1{
        color: #fff;
        font-size: 55px;
        font-weight: bolder;
    }

   
}